.App {
  text-align: center;
}

.App-body {
  margin-top: 50px;
  background-color: #f2f1e7;
  min-height: 1200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(6px + 2vmin);
}

.App-footer{
  background-color: #f2f1e7;
  min-height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 1vmin);
}

.App-header{
  position: fixed;
  top: 0;
  background-color: #f2f1e7;
  height: 50px;
  width: 100%;
  align-items: center;
  font-size: calc(10px + 1vmin);
}

button {
  background-color: #FFE156;
  padding: 3px 5px;
  border: 2px solid #333;
  box-shadow: 1px 1px 2px #555;
  cursor: pointer;
  font-size: 1em;
}

.card {
  margin: 10px;
  border-radius: 15px;
  width: 300px;
  min-height: 150px;
  box-shadow: 3px 3px 3px #555;
  background-color: #e3e0d5;
}

.card:hover {
  cursor: pointer;
}

.content {
  padding: 5px 10px;
}

.column{
  align-items: center;
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  color: black;
}

.header {
  border-radius: 15px;
  border-bottom: 2px solid #333;
  padding: 5px 8px;
  background-color: #c3d3f9;
  text-align: center;
  font-weight: 600;
  min-height: 20px;
}

.line{
  border-left: 4px solid white;
  height: inherit;
  position: absolute;
  left: 50%;
  margin-left: -2px;
}

.new-card-input {
  margin: 8px 15px;
  padding: 5px;
}

.text{
  font-size: calc(10px + 1vmin);
  border: 3px solid black;
}
